@import '../libs/font-awesome/font-awesome.min.css';
@import '../libs/base-css/reset.css';
@import '../libs/base-css/base.css';
@font-face {
  font-family: 'ProximaNova';
  src: url("../fonts/ProximaNova/ProximaNova-Bold/ProximaNova-Bold.eot");
  src: url("../fonts/ProximaNova/ProximaNova-Bold/ProximaNova-Bold.eot?#iefix") format('embedded-opentye'), url("../fonts/ProximaNova/ProximaNova-Bold/ProximaNova-Bold.woff") format('woff'), url("../fonts/ProximaNova/ProximaNova-Bold/ProximaNova-Bold.ttf") format('truetype');
  font-style: normal;
  font-weight: bold;
}
@font-face {
  font-family: 'ProximaNova';
  src: url("../fonts/ProximaNova/ProximaNova-Regular/ProximaNova-Regular.eot");
  src: url("../fonts/ProximaNova/ProximaNova-Regular/ProximaNova-Regular.eot?#iefix") format('embedded-opentye'), url("../fonts/ProximaNova/ProximaNova-Regular/ProximaNova-Regular.woff") format('woff'), url("../fonts/ProximaNova/ProximaNova-Regular/ProximaNova-Regular.ttf") format('truetype');
  font-style: normal;
  font-weight: normal;
}
@font-face {
  font-family: 'FontAwesome';
  src: url("../fonts/font-awesome/fontawesome-webfont.eot");
  src: url("../fonts/font-awesome/fontawesome-webfont.eot?#iefix") format('embedded-opentye'), url("../fonts/font-awesome/fontawesome-webfont.woff") format('woff'), url("../fonts/font-awesome/fontawesome-webfont.ttf") format('truetype');
  font-style: normal;
  font-weight: normal;
}
body {
  font-size: 16px;
  min-width: 320px;
  opacity: 1;
  overflow-x: hidden;
}
.wrapper {
  height: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}
@media screen and (max-width: 1350px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}
@media screen and (max-width: 1175px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}
@media screen and (max-width: 900px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}
@media screen and (max-width: 560px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}
header {
  padding: 15px 0;
  width: 100vw;
  height: calc(100vh - 30px);
  min-height: 500px;
  background-color: #990;
}
.box {
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  margin-left: -7.5px;
  margin-right: -7.5px;
}
.game-field,
.players {
  border-radius: 5px;
  border: 3px solid #000;
}
.game-field {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 7.5px;
  margin-right: 7.5px;
  width: calc(83.33333333333334% - 15px);
  display: flex;
  flex-direction: column;
}
.players {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 7.5px;
  margin-right: 7.5px;
  width: calc(16.666666666666668% - 15px);
  display: flex;
  flex-direction: column;
  height: 100%;
}
.players .player {
  flex-basis: 20%;
}
.players .player + .player {
  border-top: 3px solid #000;
}
.top-row {
  border-bottom: 3px solid #000;
  display: flex;
}
.middle-row {
  flex-grow: 2;
  display: flex;
}
.middle-row .col {
  display: flex;
}
.middle-row .col-left,
.middle-row .col-right {
  display: flex;
  flex-direction: column;
}
.bottom-row {
  border-top: 3px solid #000;
  display: flex;
}
.square {
  position: relative;
  width: 15%;
}
.square-content {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.square-height {
  padding-top: 100%;
}
.cell {
  width: 6.36%;
}
.col-left,
.col-right {
  width: 15%;
}
.top-border {
  border-top: 3px solid #000;
}
.left-border {
  border-left: 3px solid #000;
}
.right-border {
  border-right: 3px solid #000;
}
.col {
  width: 70%;
}
.col-inner {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  margin-left: -7.5px;
  margin-right: -7.5px;
}
.col-inner .interface {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 7.5px;
  margin-right: 7.5px;
  width: calc(75% - 15px);
}
.col-inner .text {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 7.5px;
  margin-right: 7.5px;
  width: calc(25% - 15px);
}
.col-left .cell,
.col-right .cell {
  width: 100%;
  flex-basis: 18%;
}
.col-left .cell-big,
.col-right .cell-big {
  flex-basis: 28%;
}
